
.d3-bar-chart {
  max-height: 100%;
}
.d3-bar-chart svg {
  overflow: visible;
}
.bar {
  fill: #add8e6;
  stroke: none;
}
.dummy-bar {
  fill: none;
  stroke: none;
  pointer-events: all;
}
.dummy-bar:hover {
  fill: rgba(0,0,0,0.1);
}
.bar-text {
  fill: #808080;
}
.rulers {
  stroke-width: 1px;
  stroke: #000;
}
.axis {
  stroke-width: 1px;
  stroke: #808080;
}
.line {
  stroke: rgba(0,0,0,0.5);
  stroke-width: 2px;
}
.curve {
  stroke: #000;
  stroke-width: 3px;
  fill: none;
}
.curve-point {
  fill: #808080;
  stroke: #000;
}


