
body {
  background-color: #e6ede9;
  font-size: 16px;
}
label {
  font-size: 0.8em;
  display: block;
  margin-bottom: 0.5em;
}
a {
  color: #1aad8d;
}
a:hover {
  color: #caa455;
}
a:visited {
  color: #1aad8d;
}
button,
select,
option {
  border: none;
  box-shadow: none;
  background-color: #fff;
  outline: 0;
}
button:focus,
select:focus,
option:focus {
  outline: 0;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appereance: none;
  box-sizing: border-box;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
  color: #1aad8d;
  background-image: linear-gradient(45deg, transparent 50%, #1aad8d 50%), linear-gradient(135deg, #1aad8d 50%, transparent 50%), linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), calc(100% - 2.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1px 1.5em;
  background-repeat: no-repeat;
}
button.btn {
  padding: 0.5em 2em;
  font-size: 1em;
}
button.btn,
select {
  border: #179b7e solid 1px;
  box-shadow: 1px 1px 2px rgba(0,0,0,0.5);
  border-radius: 0.25em;
  color: #1aad8d;
  font-weight: bold;
  background-color: #fff;
}
body {
  font-family: 'Asap', sans-serif;
  color: #225057;
}
#app {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.menu-container {
  position: fixed;
  bottom: 0;
  z-index: 50;
  margin: 5em;
  width: 25em;
  max-width: 25em;
}
.menu-container h1 {
  font-size: 1.7em;
  letter-spacing: 0.0325em;
  color: #caa455;
}
.menu-container div.menu {
  border: #127862 1px solid;
  border-top-width: 10px;
  border-bottom-width: 5px;
  padding: 1em 2em;
  background-color: #1aad8d;
  color: #fff;
  box-shadow: 1px 1px 2px rgba(0,0,0,0.5);
  margin-top: 1em;
  text-align: center;
  letter-spacing: 0.0625em;
}
.menu-container div.menu .title {
  border: 1.5px $lightness;
  border-style: dotted none;
  padding: 0.5em 0;
}
.menu-container div.menu h1,
.menu-container div.menu h2 {
  margin: 0 0 0.5em 0;
}
.menu-container div.menu h1 {
  font-size: 1.7em;
  letter-spacing: 0.0325em;
  font-family: 'Belgrano', serif;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.5), 1px 1px 2px rgba(0,0,0,0.5);
  color: #dcfaf3;
}
.menu-container div.menu h2 {
  font-size: 0.8em;
  font-style: italic;
}
.links {
  margin: 1em 5em 0 0;
  position: absolute;
  list-style: none;
  top: 0;
  right: 0;
}
.icon svg {
  padding: 0.1em;
  background-color: #fff;
  border-radius: 50%;
  display: inline-block;
  width: 3em;
  height: 3em;
  fill: #1aad8d;
}
.console {
  display: block;
  max-width: 20em;
  position: fixed;
  margin: 5em;
  right: 0;
  bottom: 0;
  color: #127862;
}


